___CSS_LOADER_EXPORT___.push([module.id, "\n*:not(select) {\r\n  scrollbar-color: #ffffff #2e323d;\r\n  scrollbar-width: thin;\n}\n::-webkit-scrollbar-thumb {\r\n  background: #ffffff;\n}\n::-webkit-scrollbar-track {\r\n  background-color: #2e323d;\n}\n.card-title {\r\n  color: #ffffff !important;\r\n  cursor: pointer;\n}\n.ivu-card-head {\r\n  padding: 2.5% 16px !important;\r\n  border-bottom: 1px solid #ffffff !important;\n}\n.ivu-tooltip {\r\n  border-color: #ffffff !important;\r\n  border-radius: 4px !important;\r\n  background-color: #ffffff !important;\n}\n.ivu-table {\r\n  color: #ffffff !important;\r\n  background-color: #2e323d !important;\n}\n.ivu-card, .ivu-table td, .ivu-table th {\r\n  background-color: #2e323d !important;\r\n  border-color: #ffffff !important;\n}\n.ivu-table-row-highlight, .ivu-table-row-hover {\r\n  color: #9cc3e7 !important;\n}\r\n/* Chrome and derivatives*/\n::-webkit-scrollbar {\r\n  max-width: 8px !important;\r\n  max-height: 8px !important;\n}\n.ivu-card-extra {\r\n  top: 8px !important;\n}\n.ivu-table-body {\r\n  height: 418px;\r\n  overflow-x: hidden;\r\n  scrollbar-width: thin !important;\n}\n.table-footer {\r\n  position: fixed;\r\n  bottom: 0 ;\r\n  padding-left: 10px;\r\n  width: 100%;\r\n  background-color: #ffffff;\n}\n.table-footer a {\r\n  color: #ed3f14;\n}\r\n", "",{"version":3,"sources":["webpack://./../components/Table.vue"],"names":[],"mappings":";AAsMA;EACA,gCAAA;EACA,qBAAA;AACA;AACA;EACA,mBAAA;AACA;AACA;EACA,yBAAA;AACA;AACA;EACA,yBAAA;EACA,eAAA;AACA;AACA;EACA,6BAAA;EACA,2CAAA;AACA;AACA;EACA,gCAAA;EACA,6BAAA;EACA,oCAAA;AACA;AACA;EACA,yBAAA;EACA,oCAAA;AACA;AACA;EACA,oCAAA;EACA,gCAAA;AACA;AACA;EACA,yBAAA;AACA;AACA,0BAAA;AACA;EACA,yBAAA;EACA,0BAAA;AACA;AACA;EACA,mBAAA;AACA;AACA;EACA,aAAA;EACA,kBAAA;EACA,gCAAA;AACA;AACA;EACA,eAAA;EACA,UAAA;EACA,kBAAA;EACA,WAAA;EACA,yBAAA;AACA;AACA;EACA,cAAA;AACA","sourcesContent":["<template>\r\n  <div>\r\n    <div>\r\n    <Card :bordered=\"false\" style=\"width:100%;height:100%;padding:0px\">\r\n      <div slot=\"title\" class=\"card-title\">\r\n        <span v-if=\"!showSearchInput\">\r\n          <i18n path=\"table.tips\" tag=\"span\">\r\n            <template v-slot:count>\r\n              <Badge :count=\"count\" style=\"padding:0px 5px;\"></Badge>\r\n              </template>\r\n          </i18n> - Userscript+\r\n        </span>\r\n        <Input v-else v-model=\"searchInput\"  icon=\"android-search\" placeholder=\"Enter title、description、author...\" style=\"width: 50%\"/>\r\n      </div>\r\n      <div slot=\"extra\">\r\n      <span>\r\n        <Tooltip :content=\"$t('table.search')\" placement=\"bottom\">\r\n          <Button type=\"default\" @click=\"showSearchInput = !showSearchInput\" style=\"background-color: #2e323d\">\r\n            <Icon type=\"android-search\" color=\"white\"></Icon>\r\n          </Button>\r\n        </Tooltip>\r\n        <Tooltip :content=\"$t('table.issue')\" placement=\"bottom\">\r\n          <Button type=\"default\" @click=\"open('https://github.com/magicoflolis/Userscript-Plus/issues/new')\" style=\"background-color: #2e323d\">\r\n            <Icon type=\"bug\" color=\"white\"></Icon>\r\n          </Button>\r\n        </Tooltip>\r\n        <Tooltip :content=\"$t('table.home')\" placement=\"bottom\">\r\n          <Button type=\"default\" @click=\"open('https://github.com/magicoflolis/Userscript-Plus')\" style=\"background-color: #2e323d\">\r\n            <Icon type=\"home\" color=\"white\"></Icon>\r\n          </Button>\r\n        </Tooltip>\r\n        <Tooltip :content=\"$t('table.og')\" placement=\"bottom\">\r\n          <Button type=\"default\" @click=\"open('https://github.com/jae-jae/Userscript-Plus#readme')\" style=\"background-color: #2e323d\">\r\n            <Icon type=\"fork\" color=\"white\"></Icon>\r\n          </Button>\r\n        </Tooltip>\r\n      </span>\r\n      </div>\r\n      <div>\r\n        <Table highlight-row :columns=\"columns\" :data=\"data\"></Table>\r\n        <div class=\"table-footer\"></div>\r\n      </div>\r\n    </Card>\r\n    </div>\r\n  </div>\r\n\r\n\r\n</template>\r\n<script>\r\n  import Tools from '../common/js/tools'\r\n  import Info from './Info.vue'\r\n  import Indicator from './Indicator.vue'\r\n  export default {\r\n    components: { Info, Indicator },\r\n    mounted: function () {\r\n      this.$Spin.show()\r\n      Tools.getData((json) => {\r\n        this.data = json\r\n        this.originData = json\r\n        this.count = this.data.length\r\n        this.$Spin.hide()\r\n        this.showBody = !this.showBody\r\n      })\r\n    },\r\n    data: function () {\r\n      return {\r\n        isZH: Tools.isZH(),\r\n        showSearchInput: false,\r\n        searchInput: '',\r\n        titleIcon: 'chevron-up',\r\n        count: 0,\r\n        columns: [{\r\n          type: 'expand',\r\n          width: 50,\r\n          render: (h, params) => {\r\n            return h(Info, {\r\n              props: {\r\n                row: params.row\r\n              }\r\n            })\r\n          }\r\n        },\r\n        {\r\n          type: 'index',\r\n          width: 50,\r\n          align: 'center'\r\n        },\r\n        {\r\n          title: this.$t('table.title'),\r\n          key: 'name',\r\n          width: '35%',\r\n          ellipsis: false,\r\n          render: (h, params) => {\r\n            return h('span', {\r\n              attrs: {\r\n                title: params.row.description\r\n              },\r\n              style: {\r\n                cursor: 'pointer'\r\n              },\r\n              on: {\r\n                click: _ => {\r\n                  open(params.row.url)\r\n                }\r\n              }\r\n            }, params.row.name)\r\n          }\r\n        },\r\n        {\r\n          title: this.$t('table.author'),\r\n          render: (h, params) => {\r\n            return h('span', {\r\n              attrs: {\r\n                title: this.$t('table.authorTips', {name: params.row.user.name})\r\n              },\r\n              style: {\r\n                cursor: 'pointer'\r\n              },\r\n              on: {\r\n                click: _ => {\r\n                  open(params.row.user.url)\r\n                }\r\n              }\r\n            }, params.row.user.name)\r\n          }\r\n        },\r\n        {\r\n          title: this.$t('table.dailyInstalls'),\r\n          width: 105,\r\n          key: 'daily_installs',\r\n          sortable: true,\r\n          render: (h, params) => {\r\n            return h('span', {\r\n              style: {\r\n                cursor: 'pointer'\r\n              },\r\n              on: {\r\n                click: _ => {\r\n                  open(`${params.row.url}/feedback`)\r\n                }\r\n              }\r\n            }, params.row.daily_installs)\r\n          }\r\n        },\r\n        {\r\n          title: this.$t('table.updatedTime'),\r\n          width: 105,\r\n          key: 'code_updated_at',\r\n          render: (h, params) => {\r\n            return h('span', Tools.timeagoFormat(params.row.code_updated_at))\r\n          },\r\n          sortable: true\r\n        },\r\n        {\r\n          title: this.$t('table.action'),\r\n          width: 100,\r\n          key: 'code_url',\r\n          align: 'center',\r\n          render: (h, params) => {\r\n            return h('div', [\r\n              h('Button', {\r\n                props: {\r\n                  type: 'primary',\r\n                  size: 'small',\r\n                  icon: 'ios-download-outline'\r\n                },\r\n                style: {\r\n                  marginRight: '5px'\r\n                },\r\n                on: {\r\n                  click: () => {\r\n                    this.$Message.info(this.$t('table.scriptInstalling'))\r\n                    Tools.installUserJs(params.row.code_url)\r\n                  }\r\n                }\r\n              }, this.$t('table.install'))\r\n            ])\r\n          }\r\n        }\r\n        ],\r\n        originData: [ ],\r\n        data: [ ]\r\n      }\r\n    },\r\n    watch: {\r\n      searchInput: function (val) {\r\n        (val) ? (val = val.toLowerCase(),this.data = Tools.searcher(this.originData, val)) : (this.data = this.originData);\r\n      }\r\n    },\r\n    methods: {\r\n      open (url) {\r\n        window.open(url)\r\n      }\r\n    }\r\n  }\r\n</script>\r\n\r\n<style>\r\n*:not(select) {\r\n  scrollbar-color: #ffffff #2e323d;\r\n  scrollbar-width: thin;\r\n}\r\n::-webkit-scrollbar-thumb {\r\n  background: #ffffff;\r\n}\r\n::-webkit-scrollbar-track {\r\n  background-color: #2e323d;\r\n}\r\n.card-title {\r\n  color: #ffffff !important;\r\n  cursor: pointer;\r\n}\r\n.ivu-card-head {\r\n  padding: 2.5% 16px !important;\r\n  border-bottom: 1px solid #ffffff !important;\r\n}\r\n.ivu-tooltip {\r\n  border-color: #ffffff !important;\r\n  border-radius: 4px !important;\r\n  background-color: #ffffff !important;\r\n}\r\n.ivu-table {\r\n  color: #ffffff !important;\r\n  background-color: #2e323d !important;\r\n}\r\n.ivu-card, .ivu-table td, .ivu-table th {\r\n  background-color: #2e323d !important;\r\n  border-color: #ffffff !important;\r\n}\r\n.ivu-table-row-highlight, .ivu-table-row-hover {\r\n  color: #9cc3e7 !important;\r\n}\r\n/* Chrome and derivatives*/\r\n::-webkit-scrollbar {\r\n  max-width: 8px !important;\r\n  max-height: 8px !important;\r\n}\r\n.ivu-card-extra {\r\n  top: 8px !important;\r\n}\r\n.ivu-table-body {\r\n  height: 418px;\r\n  overflow-x: hidden;\r\n  scrollbar-width: thin !important;\r\n}\r\n.table-footer {\r\n  position: fixed;\r\n  bottom: 0 ;\r\n  padding-left: 10px;\r\n  width: 100%;\r\n  background-color: #ffffff;\r\n}\r\n.table-footer a {\r\n  color: #ed3f14;\r\n}\r\n</style>"],"sourceRoot":""}]);
